* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

img {
  object-fit: fill;
  width: 100%;
  height: 200px;
  display: block;
}

.header {
  width: 100%;
  z-index: 10;
  opacity: 1;
  background-image: linear-gradient(90deg, #9c9094 0%, #312d3c 100%);
  position: fixed;
}

.section {
  padding-top: 120px;
  padding-bottom: 40px;
}

.on-scroll {
  opacity: .9;
}

.wrapper {
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
}

.search-form {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form-field {
  position: relative;
}

input {
  height: 40px;
  width: 300px;
  color: #2a2438;
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 5px;
  font-family: Tilt Neon, cursive;
  transition: border-color .3s ease-in-out;
}

.btn-search {
  height: 40px;
  width: 60px;
  cursor: pointer;
  color: #fff;
  background-color: #9896f1;
  border: 2px solid #9896f1;
  border-radius: 0 5px 5px 0;
  transition: background-color .3s ease-in-out;
}

.btn-search:hover, .btn-search:focus .load-more:focus, .load-more:hover {
  background-color: rgba(152, 150, 241, .5);
}

.btn-clear {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-items: center;
  position: absolute;
  top: 10px;
  right: 10px;
}

.form-icon {
  fill: red;
}

.load-more {
  height: 40px;
  width: 100px;
  cursor: pointer;
  color: #fff;
  background-color: #9896f1;
  border: 2px solid #9896f1;
  border-radius: 5px;
  margin: 0 auto;
  transition: background-color .3s ease-in-out;
  display: block;
}

